  align-items: center;
  display: flex;
}

.gallery-wrapper {
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 0 10px;
  display: grid;
}

.load-more {
  max-width: 120px;
